import React, { Component } from 'react'
import ReactDOM from "react-dom";
import "bootstrap/dist/css/bootstrap.min.css";
import { Grid, Row, Col, Table } from "react-bootstrap";

import { Bar, Line, Pie, Scatter } from 'react-chartjs-2';
import React_PDF from "../pdf/React_PDF";

import jsPDF from 'jspdf';

import html2canvas from "html2canvas";




export default class Chartjs extends Component {
    constructor(props) {
        super(props)
    
        this.state = {
            chartRender : props.chart,

            loading: false,
             
        }
    }


    
    div2PDF = e => {

        this.setState({ loading: true });


        const KanitRegularNormal = this.fontBinary();
        const doc = new jsPDF({filters: ['ASCIIHexEncode']})


        doc.addFileToVFS("Kanit-Regular-normal.ttf", KanitRegularNormal);
        doc.addFont('Kanit-Regular-normal.ttf', 'Kanit-Regular', 'normal');

        doc.setFont('Kanit-Regular');

        doc.text("Hello world! สวัสดี", 10, 10);

        /////////////////////////////
        // Hide/show button if you need
        /////////////////////////////
    
        const but = e.target;
        but.style.display = "none";
        let input = window.document.getElementsByClassName("div2PDF")[0];
        let pagebreak = input.getElementsByClassName("pagebreak");

        let test : string = 'test';
        test = 123;

        console.log(test,'test')



        
        // console.log(input,'input');
        // console.log(pagebreak,'pagebreak');
    
        html2canvas(input).then(canvas => {
            var imgData = canvas.toDataURL('image/png');

            console.log(canvas,'canvas')

            /*
            Here are the numbers (paper width and height) that I found to work. 
            It still creates a little overlap part between the pages, but good enough for me.
            if you can find an official number from jsPDF, use them.
            */
            var imgWidth = 210; 
            var pageHeight = 295;  
            var imgHeight = canvas.height * imgWidth / canvas.width;
            var heightLeft = imgHeight;
      
            var doc = new jsPDF('l', 'mm', 'a4');
            var position = 0;
      
            doc.addImage(imgData, 'PNG', 0, position, imgWidth, imgHeight);
            heightLeft -= pageHeight;
      
            while (heightLeft >= 0) {
              position = heightLeft - imgHeight;
              doc.addPage();
              doc.addImage(imgData, 'PNG', 0, position, imgWidth, imgHeight);
              heightLeft -= pageHeight;
            }
            doc.save( 'Cloud Operation.pdf');
            
            this.setState({ loading: false });

        });

    };


    render() {

        const { loading } = this.state;

        // const KanitRegularNormal = this.fontBinary();
        // const doc = new jsPDF({filters: ['ASCIIHexEncode']})


        // doc.addFileToVFS("Kanit-Regular-normal.ttf", KanitRegularNormal);
        // doc.addFont('Kanit-Regular-normal.ttf', 'Kanit-Regular', 'normal');

        // doc.setFont('Kanit-Regular');

        // doc.text("Hello world! สวัสดี", 10, 10);
        // doc.save("a4.pdf");



        return (
            <div>

                <Col md={2}>
                <div>
                    <button onClick={(e) => this.div2PDF(e)}
                    id="chart2PDF" className="btn btn-primary btn-md" role="button" disabled={loading}
                    >
                        { loading && (
                            <i
                            className="fa fa-refresh fa-spin"
                            style={{ marginRight: "5px" }}
                            />
                        )}
                        {loading && <span>Loading...</span>}
                        {!loading && <span>Export PDF</span>}
                    </button>
                </div>
                </Col>

                <div className="div2PDF">
                    {this.state.chartRender.map((item) => {
                        // console.log(item.vmname,'vmname นะจ๊ะ');
                        // console.log(item.chart,'Chart นะจ๊ะ');
                        return item.chart.map((n,index) => {
                            return (
                                <div className="pagebreak">
                                    {item.vmname}
                                    <Line key={index} data={n} />
                                </div>
                                )

                        })
                    })}
                </div>

            </div>
        )
    }


    fontBinary = () => {
        
        return ""
    }




}
